<template>
  <div class="page-questions">
    <div
        class="question"
        v-for="(item, index) in questions"
        :key="index"
        @click="ask(item.question, item.answer, index, item.hint)"
        :class="[{'hide': !item.visible}, `question-${index}`]"
    >
      <template  v-if="item.visible">
        Кликни!
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: "PAgeQuestions",
  data() {
    return {
      questions: [
        {
          question: 'Ты моя Мэйбл - я твой ...',
          answer: 'пухля',
          visible: true
        },
        {
          question: 'Ты мой Диппер - я твой ...',
          answer: 'дневник',
          visible: true
        },
        {
          question: 'Ты мой Зус - я твой ...',
          answer: 'знак вопроса',
          visible: true
        },
        {
          question: 'Ты мой Стэн - я твоя ...',
          answer: 'хижина чудес',
          hint: 'Ну полное ж название',
          visible: true
        },
        {
          question: 'Ты моя Черная жемчужина - я твой ...',
          answer: 'капитан джек воробей',
          hint: 'Капитан...',
          visible: true
        },
        {
          question: 'Ты моя Бонни - я твой ...',
          answer: 'клайд',
          visible: true
        },
        {
          question: 'Ты мой кот - я твои ...',
          answer: 'сапоги',
          visible: true
        },
        {
          question: 'Ты мой свитер - я твой ...',
          answer: 'пухля',
          hint: 'хрю-хрю',
          visible: true
        },
        {
          question: 'Ты моя Фиона - я твой ...',
          answer: 'шрек',
          visible: true
        }
      ]
    }
  },
  methods: {
    ask(question, answer, index, hint) {
      const enteredAnswer = prompt(question);
      if (enteredAnswer.toLowerCase().trim() === answer) {
        this.questions[index].visible = false
      } else {
        alert('Не верно! ' + (hint? `Подсказка: ${hint}` : ''));
      }
    }
  }
}
</script>

<style scoped lang="scss">
.page-questions {
  width: 300px;
  height: 600px;
  background: url("~@/assets/questions-bg.jpg") no-repeat center center;
  background-size: cover;
  position: relative;
}

.hide {
  display: none !important;
}

.question {
  width: 100px;
  height: 200px;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  &-1 {
      top: 0;
      left: 100px;
   }

  &-2 {
    top: 0;
    left: 200px;
  }

  &-3 {
    top: 200px;
    left: 0;
  }

  &-4 {
    top: 200px;
    left: 100px;
  }

  &-5 {
    top: 200px;
    left: 200px;
  }

  &-6 {
    top: 400px;
    left: 0;
  }

  &-7 {
    top: 400px;
    left: 100px;
  }

  &-8 {
    top: 400px;
    left: 200px;
  }
}
</style>